/* eslint-disable */

<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div v-if="this.signin ==='Sign in with Twitter'">
      <button v-on:click="handleTwitterLoginClick">
        <img src="@/assets/twitter.svg" width="30px" />
        <span class="user">{{signin}}</span>
      </button>
    </div>
    <div v-else>{{signin}}</div>
  </div>
</template>

<script>

import * as axios from 'axios';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
   data () {
    return {
      hand: null,
      loading: true,
      errored: null,
      signin: "Sign in with Twitter"
    }
  },
  methods: {
      handleTwitterLoginClick: function(event){

      const self = this;
      const url = (document.domain === "127.0.0.1") ? 'http://127.0.0.1:5000/twitter/auth' : 'https://probe.now.sh/twitter/auth'

      axios.get(url).then( response => {
            //
            console.log("Twitter response: ", response.data);

            self.signin = "welcome " + response.data.screen_name;
           
          }
        ).catch(error => {

          console.log("Twitter error: ", error);
         
          this.errored = error
        }).finally(() => this.loading = false);

    }
  }
}
</script>

